import React from 'react'
import Register from '../../Forms/Register/Register'
import 'bulma/css/bulma.css'
import './Modal.css'

const Modal = (props) => (
<div className={props.modalManage ? "modal is-active" : "modal"}>
  <div className="modal-background" onClick={() => props.modalManage(false)}></div>
  <div className="modal-content">
    <div className="box">
        <Register buttonLoading={props.buttonLoading} toggleButton={props.toggleButton} />
    </div>
  </div>
  <button className="modal-close is-large" aria-label="close" onClick={() => props.modalManage(false)}></button>
</div>
);

export default Modal;


