import * as actionsType from './actionsType';

export const itemSelect = item => {
    return {
        type: actionsType.ITEM_SELECT,
        route: item
    }
}

export const handleScrolling = item => {
    return {
        type: actionsType.HANDLE_SCROLLING,
        scroll: item
    }
}

export const modalManage = stat => {
    return {
        type: actionsType.MODAL_MANAGE,
        status: stat
    }
}

export const sideBarManage = stat => {
    return {
        type: actionsType.SIDEBAR_MANAGE,
        status: stat
    }
}

export const authorized = stat => {
    return {
        type: actionsType.AUTHORIZED,
        status: stat
    }
}

export const userRegistered = () => {
    return {
        type: actionsType.USER_REGISTERED
    }
}

export const isStillSigin = () => {
    return {
        type: actionsType.IS_STILL_SIGN_IN
    }
}



