import React from 'react'
import Aux from '../../../hoc/.Aux/Aux'
import 'bulma/css/bulma.css'
import './Navbar.css'
import NavBarLogo from './NavBarLogo/NavBarLogo'
import NavBarItems from './NavBarItems/NavBarItems.js'

const NavBar = (props) => {
       const opacityEffect = {
        backgroundColor: 'rgba(255,255,255,0)',
        boxShadow: '0 0 0',
        color: 'black',
        fontWeight: 'bold'
       }
       return (
        <Aux>
            <div className="navbar is-flex-touch" 
                style={props.onTop ? opacityEffect : {} }
                >
                <div className="navbar-brand">
                    <div className="navbar-burger" onClick={() => props.onSideBar(true)}>
                        <span></span>
                        <span></span>
                        <span></span>
                    </div>  
                    <NavBarLogo className="brandlogo" />
                </div>
    
                <div className="navbar-menu">
                    <NavBarItems 
                    isSideBar={props.isSideBar} 
                    scrollNav={props.scrollNav} 
                    isItemSelect={props.isItemSelect}
                    />
                    {props.isAuthorized ?<Aux><div>
                        <a 
                        style={props.onTop ? {fontWeight: "normal"}: {fontWeight: "normal"}} 
                        className="button is-link is-rounded"
                        onClick={() => props.onRouteChange('dashboard')}
                        >
                        Dashboard
                        </a>
                    </div><div 
                    onClick={() => props.onAuthorized(false)} 
                    className="signinButton" 
                    style={props.onTop ? {fontWeight: "normal", textDecoration: "underline"}: {fontWeight: "normal", textDecoration: "underline"}} 
                    >
                    sign out
                    </div></Aux> : <Aux><div 
                    onClick={() => props.onRouteChange('signin')} 
                    className="signinButton" 
                    style={props.onTop ? {fontWeight: "normal", textDecoration: "underline"}: {fontWeight: "normal", textDecoration: "underline"}} 
                    >
                    sign in
                    </div>
                    <div>
                        <a 
                        style={props.onTop ? {fontWeight: "normal"}: {fontWeight: "normal"}} 
                        className="button is-link is-rounded" 
                        onClick={() => props.activeModal(true)}>
                        Sing Up
                        </a>
                    </div></Aux>}
                </div>
            </div>
        </Aux>
        );
}

export default NavBar



