import React from 'react';
import './SignInForm.css'

const SingInForm = (props) => {
    return (
    <div className="signinbox">
        <div style={{backgroundColor: 'rgba(255,255,255, 0.4)'}} className="box signinform">
            <p className="title has-text-centered" style={{color: "black"}}>Sign In</p>
            <div className="field">
                <label className="label">Username</label>
                <div className="control">
                    <input className="input" value={props.user} type="text" placeholder="username" onChange={props.onUserChange} />
                </div>
            </div>
            <div className="field">
                <label className="label">Password</label>
                <div className="control">
                    <input className="input" type="password" value={props.pass} placeholder="password" onChange={props.onPasswordChange} />
                </div>
            </div>
            {props.validSignIn === 'true' ? null : props.validSignIn === 'false' ? <p className="help is-danger">{props.errorSigIn}</p> : null}
            {props.userEmpty === 'true' || props.passEmpty === 'true' ? <p className="help is-danger">Please enter username and password</p> : null}
            {props.globalError.length ? <p className="help is-danger">{props.globalError}</p> : null}
            <div className="buttonContent"><div style={{cursor: "pointer"}} className="button is-warning" onClick={props.onSubmit}>Enter</div></div>
        </div>
    </div>
    )
}

export default SingInForm


