import React from 'react'
import './Aboutus.css'
//import image from '../assets/img/photo.jpg'

const AboutUs = (props) => {
 
    return(
            <section id="aboutus" className="hero is-fullheight" style={{backgroundColor: "white", boxShadow: "5px black"}}>
                <div className="hero-body">
                    <div className="container">
                    <h1 className="title">
                        About us
                    </h1>
                    <div className="media">
                        <div className="media-left">
                            <figure className="image is-128x128">
                                <img className="is-rounded" src="https://bulma.io/images/placeholders/128x128.png" alt="profile" />
                            </figure>           
                        </div>
                        <div className="media-content">
                            <h2 className="subtitle">
                            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
                            </h2>
                        </div>
                    </div>
                    </div>
                </div>
            </section>
        )
}

export default AboutUs;


