import * as actionTypes from '../actions/actionsType';

const initalState = {
    route: 'home',
    isModal: false,
    scrollNav: 'home',
    isSideBar: false,
    authorized: false,
}

export const reducerUI = (state = initalState, action) => {
    switch (action.type) {
        case actionTypes.ITEM_SELECT:
         return {
            ...state,
            route: action.route
        }
        case actionTypes.HANDLE_SCROLLING:
            const event = action.scroll;
            const scroll = event.currentTarget.scrollY;
            const height = document.documentElement.scrollHeight - document.documentElement.clientHeight
            const section = height / 3
            const sum = section+section
            if (scroll < section) {
              window.removeEventListener('scroll', null)
              return {
                ...state,
                scrollNav: 'home',
              }
            } else if (scroll > section && scroll < sum ) {
              window.removeEventListener('scroll', null)
              return {
                ...state,
                scrollNav: 'aboutus',
              }
            } else if (scroll > sum) {
              window.removeEventListener('scroll', null)
              return {
                ...state,
                scrollNav: 'contactus',
              }
            }
            break;
        case actionTypes.MODAL_MANAGE:
            return {
                ...state,
                isModal: action.status
            }
        case actionTypes.SIDEBAR_MANAGE:
            return {
                ...state,
                isSideBar: action.status
            }
        case actionTypes.AUTHORIZED:
            return {
                ...state,
                authorized: action.status
            }
        case actionTypes.USER_REGISTERED:
            return {
                ...state,
                isModal: false,
                authorized: true
            }
        case actionTypes.SIGN_IN_SUCCESS: {
            return {
                ...state,
                authorized: true,
                route: 'home'
            }
        }
        case actionTypes.IS_STILL_SIGN_IN:
        return {
            ...state,
            authorized: true
        }
        default:
            return state
    }
}

const registerState = {
    isUserExist: '',
    isEmailExist: '',
    userMessage: '',
    registerMessage: '',
    status: '',
    error: ''
}

export const reducerReg = (state = registerState, action) => {
    switch(action.type) {
        case actionTypes.USER_EXIST: 
            return {
                ...state,
                isUserExist: 'true'
            }
        case actionTypes.USER_NOT_EXIST:
            return {
                ...state,
                isUserExist: 'false',
                userMessage: action.message,
            }
        case actionTypes.EMAIL_EXIST:
            return {
                ...state,
                isEmailExist: 'true'
            }
        case actionTypes.EMAIL_NOT_EXIST:
            return {
                ...state,
                isEmailExist: 'false'
            }
        case actionTypes.USER_REGISTERED_FAIL: {
            return {
                ...state,
                registerMessage: action.error
            }
        }   
        default:
            return state
        }   
}

const signinState = {
    username: '',
    validSignIn: '',
    errorSigIn: '',
    globalError: ''

}

export const reducerSig = (state = signinState, action) => {
    switch(action.type) {
        case actionTypes.SIGN_IN_SUCCESS:
            return {
                ...state,
                username: action.username
            }
        case actionTypes.SIGN_IN_FAIL:
            return {
                ...state,
                validSignIn: 'false',
                errorSigIn: action.message
            }
        case actionTypes.SIGN_IN_GLOBAL_ERROR:
            return {
                ...state,
                globalError: `${action.errorGet}`
            }
        default:
            return state
    }
}


