import * as actionTypes from './actionsType'
import axios from 'axios';


export const signinResult = data => {
    if (data.data.username) {
        return {
            type: actionTypes.SIGN_IN_SUCCESS,
            username: data.username
        }
    } else {
        return {
            type: actionTypes.SIGN_IN_FAIL,
            message: data.error    
        }
    }
}

export const globalError = error => {
    return {
        type: actionTypes.SIGN_IN_GLOBAL_ERROR,
        errorGet: error

    }
}

export const signin = data => {
    return dispatch => {
        return axios.post('https://learnkoreanserver.herokuapp.com/login', {
				username: data.username,
				password: data.password
		})
        .then(json => dispatch(signinResult(json)))
        .catch(error => dispatch(globalError(error)))
        }
    }


