import * as actionTypes from './actionsType'

export const sendResult = (validate, label) => {
    if (label === 'user') {
        if(validate.username) {
            return {
                type: actionTypes.USER_EXIST,
            }
        } else {
            return {
                type: actionTypes.USER_NOT_EXIST,
                message:validate.message
            }
        }  
    } else if (label === 'email') {
        if (validate.email) {
            return {
                type: actionTypes.EMAIL_EXIST,
            }
        } else {
            return {
                type: actionTypes.EMAIL_NOT_EXIST,
            }
        }
    }
}

export const userRegistered = user => {
    if (user.id) {
        return {
            type: actionTypes.USER_REGISTERED,
        }
    } else {
        return {
            type: actionTypes.USER_REGISTERED_FAIL,
            message: user.error
        }
    }
}

export const checkUser = (data, label) => {
    if(label === "user") {
    return dispatch => {
        return fetch(`https://learnkoreanserver.herokuapp.com/users/${data}`)
        .then(response => response.json())
        .then(json => {
            dispatch(sendResult(json, label))
        })
    }
  } else if (label === "email") {
    return dispatch => {
        return fetch(`https://learnkoreanserver.herokuapp.com/email/${data}`)
        .then(response => response.json())
        .then(json => {
            dispatch(sendResult(json, label))
        })
    } 
  }
}

export const registerUser = data => {
    return dispatch => {
        return fetch('https://learnkoreanserver.herokuapp.com/register', {
			method: 'post',
			headers: {'Content-Type': 'application/json'},
			body: JSON.stringify({
				username: data.username,
                password: data.password,
                name: data.name,
                email: data.email
			})
		})
        .then(response => response.json())
        .then(json => {
                dispatch(userRegistered(json))
        })
    }
}



