import React, { Component } from 'react'
import './NavBarItems.css'
import 'bulma/css/bulma.css'

class NavBarItems extends Component {
    state = {
        menuObj: {
                home: {
                    id: 'home',
                    value: 'Home',
                    isActive: true
                },
                aboutus: {
                    id: 'aboutus',
                    value: 'About us',
                    isActive: null
                },
                contactus: {
                    id: 'contactus',
                    value: 'Contact us',
                    isActive: null
                },
        },
        buttoFirst: null
    }

    itemStatus = (act, id, array) => {
        let status = Object.keys(array).filter(item => {
            if(act === 'active') {
                return array[item].id === id
            } else if (act === 'unactive') {
                return array[item].id !== id
            } else {
                return null
            }
        })
        return status;
    }

    
    isActive = (id) => {
        window.removeEventListener('scroll', this.buttonToFalse)
        this.setState({
            buttoFirst: true
        })
        const copyMenu = this.state.menuObj;

        const activating = this.itemStatus('active', id, copyMenu);
        const unactivating = this.itemStatus('unactive', id, copyMenu)
        let active = activating[0]

        this.setState({
            menuObj: {
                ...copyMenu,
                [active]: {
                    ...copyMenu[active],
                    isActive: true
                },
                [unactivating[0]]: {
                    ...copyMenu[unactivating[0]],
                    isActive: false
                },
                [unactivating[1]]: {
                    ...copyMenu[unactivating[1]],
                    isActive: false
                } 
            }
        })
          this.props.isItemSelect(id) 
          window.addEventListener('scroll', this.buttonToFalse)
        }

        componentDidMount = () => {
            if(this.props.isSideBar) {
                return null
            }
            window.addEventListener('scroll', this.buttonToFalse)
        }
        
        componentWillUnmount = () => {
            window.removeEventListener('scroll', this.buttonToFalse)
        }

        buttonToFalse = () => {
            this.setState({
                buttoFirst: false
            })
            this.scrollingChange()
        }


        scrollingChange = () => {
            if(this.state.buttoFirst) {
                console.log('waiting')
            } else {
            const copyMenu = this.state.menuObj
            const scrolling = this.itemStatus('active', this.props.scrollNav, copyMenu )
            const noScrolling = this.itemStatus('unactive', this.props.scrollNav, copyMenu )

            let scrollActive = scrolling[0]
            this.setState({
                menuObj: {
                    ...copyMenu,
                    [scrollActive]: {
                        ...copyMenu[scrollActive],
                        isActive: true
                    },
                    [noScrolling[0]]: {
                        ...copyMenu[noScrolling[0]],
                        isActive: false
                    },
                    [noScrolling[1]]: {
                        ...copyMenu[noScrolling[1]],
                        isActive: false
                    } 
                }
            })
          }
        }


    render() {
        const selectItems = Object.keys(this.state.menuObj).map(items =>{
            return <a 
            style={{color: "black", fontWeight: 'bold'}} 
            href={`#${items}`} 
            key={items}>
                <li 
                style={{textAlign: "center"}} 
                className={ this.state.menuObj[items].isActive ? "navbar-item active" : "navbar-item"} 
                onClick={() => this.isActive(this.state.menuObj[items].id)}>
                    {this.state.menuObj[items].value}
                </li>
            </a>
        })
        return (
            <div className="menu-items navbar-end">
                <ul>
                   {selectItems}
                </ul>
            </div>
        )
    }
}

export default NavBarItems


