import React, { Component } from 'react'
import { connect } from 'react-redux';
import { checkUser, registerUser } from '../../../store/actions/register'
import './Register.css'

class Register extends Component {

    state = {
        isUserValid: '',
        isUserFieldEmpty: true,
        isEmailFieldEmpty: true,
        isEmailValid: '',
        isNameValid: '',
        isPasswordValid: '',
        users: {
            name: '',
            username: '',
            email: '',
            password: ''
        },
        invalidSubmit: false,
        isLoading: false
    }

    isUserValid = (event) => {
        const user = event.target.value;
        if (user.length < 1) {
            this.setState({
                isUserFieldEmpty: true
            })
        } else if (user.length >= 1) {
            this.setState({
                isUserFieldEmpty: false,
                invalidSubmit: false
            })
            if (user.length > 3) {
                this.props.onValidateUser(user)
                this.setState({
                    isUserValid: 'true',
                    users: {
                        ...this.state.users,
                        username: user
                    }
                })
            } else {
                this.setState({
                    isUserValid: 'false',
                })
            }
        }
    }

    validatingEmail = (email) => {
        const regemail = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return regemail.test(email)
    }

    validatingPassword = (pass) => {
        const regpass = /^(?=.*\d).{4,8}$/;
        return regpass.test(pass)
    }

    onEmailChange = (event) => {
        const email = event.target.value
            if(email.length < 1) {
                this.setState({
                    isEmailFieldEmpty: true
                })
            } else if (email.length >= 1) {
                this.setState({
                    isEmailFieldEmpty: false,
                    invalidSubmit: false
                })
                if(this.validatingEmail(email)) {
                this.setState({
                    users: {
                        ...this.state.users,
                        email: email
                    },
                    isEmailValid: 'true'
                })
                } else {
                    this.setState({
                        isEmailValid: 'false'
                    })
                }
            }   
    }

    onNameChange = (event) => {
        this.setState({
            users: {
                ...this.state.users,
                name: event.target.value
            },
            invalidSubmit: false
        })
    }

    onPassChange = (event) => {
        this.setState({
            users: {
                ...this.state.users,
                password: event.target.value
            },
            invalidSubmit: false
        })
    }



    verifyEmail = () => {
            if(this.state.isEmailValid === 'false' || this.state.isEmailValid === '') {
                this.setState({
                    isLoading: false
                })
                return false
            } else if (this.state.isEmailValid === 'true') {
                return true
            }   
    }

    verifyName = () => {
        if(this.state.users.name.length < 1) {
            this.setState({
                isNameValid: 'false',
                isLoading: false
            })
            return false
        } else if (this.state.users.name.length > 1) {
            return true
        }
    }

    verifyPassword = () => {
        if(this.validatingPassword(this.state.users.password)) {
            return true
        } else {
            this.setState({
                isPasswordValid: 'false',
                isLoading: false
            })
            return false
        }
    }

    onGoodSubmit = () => {
        this.setState({
            isLoading: true
        })
        const emailVerified = this.verifyEmail()
        if(emailVerified) {
            const nameVerified = this.verifyName()
            if(nameVerified) {
                const passwordVerified = this.verifyPassword()
                if(passwordVerified) {
                    this.props.onValidateEmail(this.state.users.email)
                    return setTimeout(() => {
                        if (this.props.isEmailExist === 'true') {
                        this.setState({
                            isLoading: false,
                            isUserFieldEmpty: true
                        })
                    } else if (this.props.isEmailExist === 'false') {
                        this.setState({
                            isLoading: false,
                            invalidSubmit: false
                        })
                        this.props.onSubmitForm(this.state.users)
                    } else {
                        console.log('NOT ENTER')
                    }
                }, 3000)

                } else {
                    this.setState({
                        isPasswordValid: 'false',
                        isLoading: false
                    })
                }
            } else {
                this.setState({
                    isNameValid: 'false',
                    isLoading: false
                })
            }
        } else {
            this.setState({
                isLoading: false,
                invalidSubmit: true
            })
        }  
    }

    render() {
        return (
    <div>
        {this.state.isLoading ? <div className="spinner"><div className="double-bounce1"></div><div className="double-bounce2"></div></div> : <div><p className="title has-text-centered" style={{color: "black"}}>Sign Up</p>
        <div className="field">
            <label className="label">Name</label>
            <div className="control">
                <input className="input" type="text" placeholder="Your name" onChange={this.onNameChange} />
            </div>
            {this.state.isNameValid === 'false' ? <p className="help is-danger">Invalid Name</p>: null}
        </div>
        <div className="field">
            <label className="label">Username</label>
            <div className="control has-icons-left has-icons-right">
                <input className="input" type="text" placeholder="Username" onChange={this.isUserValid} />
                <span className="icon is-small is-left">
                <i className="fas fa-user"></i>
                </span>
            </div>
            {this.state.isUserFieldEmpty ? 
                null :
                 this.state.isUserValid === 'false'?
                  <p className="help is-danger">Username is not valid</p> :
                   this.state.isUserValid === 'true' && this.props.isUserExist === 'true'?
                    <p className="help is-danger">Username already exist</p> :
                     this.state.isUserValid === 'true' && this.props.isUserExist === 'false' ?
                      <p className="help is-success">{this.props.message}</p> :
                       null}
        </div>
        <div className="field">
            <label className="label">Email</label>
            <div className="control has-icons-left has-icons-right">
                <input className="input" type="email" placeholder="Email" onChange={this.onEmailChange} />
                <span className="icon is-small is-left">
                <i className="fas fa-envelope"></i>
                </span> 
            </div>
            {this.state.isEmailFieldEmpty ? null : this.state.isEmailValid === 'true' ? null : this.state.isEmailValid === 'false' ? <p className="help is-danger">Email is not valid</p> : null}
        </div>
        <div className="field">
            <label className="label">Password</label>
            <div className="control has-icons-left has-icons-right">
                <input className="input" type="password" placeholder="password" onChange={this.onPassChange} />
            </div>
            {this.state.isPasswordValid === 'false' ? <p className="help is-danger">Invalid Password</p>: null}
        </div>
            <a className="button is-info" 
            style={{left: "40%"}} 
            onClick={this.onGoodSubmit}
            >Submit</a>
            {this.props.isEmailExist === 'true' ? <p className="help is-danger">Email already register</p> : null}
            {this.props.registerMessage.length ?<p className="help is-danger">{this.props.registerMessage}</p> : null}
            {this.state.invalidSubmit ? <p className="help is-danger">Please fill the registration form with data</p> : null}
        </div>}
    </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        isUserExist: state.redreg.isUserExist,
        error: state.redreg.error,
        isEmailExist: state.redreg.isEmailExist,
        message: state.redreg.userMessage,
        registerMessage: state.redreg.registerMessage
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onValidateUser: (data) => dispatch(checkUser(data, "user")),
        onValidateEmail: (data) => dispatch(checkUser(data, "email")),
        onSubmitForm: (data) => dispatch(registerUser(data))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Register);


