import React, { Component } from 'react';
import { connect } from 'react-redux';

import Aux from '../../hoc/.Aux/Aux';
import SignInForm from '../Forms/SignIn/SignInForm'
import { signin } from '../../store/actions/signin'

import './Signin.css'

class SignIn extends Component {
    state = {
        user: {
            username: '',
            password: ''
        },
        userEmpty: '',
        passEmpty: '',
        validUser: true
    }

    onUserChange = (event) => {
        this.setState({
            ...this.state,
            user: {
                ...this.state.user,
                username: event.target.value
            },
            userEmpty: 'false',
            passEmpty: 'false'
        })
    }
    
    onPasswordChange = (event) => {
        this.setState({
            ...this.state,
            user: {
                ...this.state.user,
                password: event.target.value
            },
            userEmpty: 'false',
            passEmpty: 'false'
        })
    }

    onSubmit = () => {
        const username = this.state.user.username;
        const password = this.state.user.password;
        if(username.length > 1) {
            if(password.length > 1) {
                this.setState({
                    ...this.state,
                    passEmpty: 'false',
                    userEmpty: 'false'
                })
                this.props.onSubmitForm(this.state.user)
            } else {
                this.setState({
                    ...this.state,
                    passEmpty: 'true'
                })
            }
        } else {
            this.setState({
                ...this.state,
                userEmpty: 'true'
            })
        }
            
    }

    render() {
        return (
            <Aux>
                <div className="signin">
                    <span 
                    style={{cursor: "pointer"}} 
                    onClick={() => this.props.outSignIn('home')}>
                    <i style={{margin: "10px", opacity: "0.7"}} className="fas fa-chevron-circle-left fa-3x">
                    </i></span>
                    <SignInForm 
                    userEmpty={this.state.userEmpty} 
                    passEmpty={this.state.passEmpty} 
                    validSignIn={this.props.validSignIn} 
                    user={this.state.username} 
                    pass={this.state.password}  
                    onUserChange={this.onUserChange} 
                    onPasswordChange={this.onPasswordChange} 
                    onSubmit={this.onSubmit}
                    errorSigIn={this.props.errorSigIn} 
                    globalError={this.props.globalError}
                    />
                </div>
            </Aux>
        );
    }
}

const mapStateToProps = state => {
    return {
        validSignIn: state.sig.validSignIn,
        errorSigIn: state.sig.errorSigIn,
        globalError: state.sig.globalError
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onSubmitForm: (data) => dispatch(signin(data))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SignIn);


