import React from 'react';
import 'bulma/css/bulma.css'
import './Home.css'
import Info from '../Info/Info'
import Register from '../../Forms/Register/Register'



const Home = () => {
        return(
         <section id="home" className="hero is-success is-fullheight bgimg">
            <div className="hero-body">
                <div className="container">
                    <div className="columns level">
                        <div className="column is-two-fifths"><Info /></div>
                        <div className="column box is-two-fifths level-right"><Register /></div>
                    </div>
                </div>
            </div>
         </section>
        )
}

export default Home;


