import React from 'react'
import 'bulma/css/bulma.css'
import './SideBar.css'
import logo from '../../assets/img/school.png'



const SideBar = (props) => {
    const changeRoute = (route) => {
        props.onRouteChange(route)
        props.closeSideBar()
    }
    const style={
        position: "fixed",
        zIndex: "200",
        width: "100%",
        height: "100%",
        top: "0", left: "0",
        background: "rgba(128, 128, 128, 0.8)"
    }
return (
        <div>
            <div className="backdrop" style={style} onClick={() => props.SideBarManage(false)}></div>
            <div style={{transform: props.isSideBar ? "translateX(0)" : "translateX(-100vw)"}} className="sidebar box">
                <aside className="menu">
                    <p style={{width: "100%", height: "100%", boxSizing: "border-box", display: "flex", justifyContent: "center"}} className="menu-label">
                            <img style={{width: "50%", height: "50%"}} src={logo} alt="learnkorean" />
                    </p>
                    <hr />
                    <ul className="menu-list">
                        <li onClick={() => changeRoute('home')}><a href='#home'>Home</a></li>
                        <li onClick={() => changeRoute('aboutus')}><a href='#aboutus'>About us</a></li>
                        <li onClick={() => changeRoute('contactus')}><a href='#contactus'>Contact us</a></li>
                    </ul>
                </aside>
            </div>
        </div>
)
}

export default SideBar


