import React, { Component } from 'react';
import { connect } from 'react-redux';

import Aux from '../hoc/.Aux/Aux'
import Home from '../components/Home/Home/Home'
import NavBar from '../components/Navigation/Navbar/Navbar'
import Modal from '../components/UI/Modal/Modal'
import AboutUs from '../components/Aboutus/Aboutus'
import ContactUs from '../components/ContactUs/ContactUs'
import Footer from '../components/Footer/Footer'
import SideBar from '../components/Navigation/SideBar/SideBar'
import SignIn from '../components/Signin/Signin'
import Dashboard from '../components/Dashboard/Dashboard';
import * as UI from '../store/actions/UI'

import 'bulma/css/bulma.css'




class App extends Component {
  state = {
    onTop: true,
  }


  isUserloggedIn = () => {
    let user = localStorage.getItem('user')
    if(user) {
      this.componentDidMount = () => {
        window.addEventListener('load', this.props.isStillSignin())
      }
    } else {
      return null
    }
  }

  componentDidMount = () => {
      window.addEventListener('scroll', this.handleScroll)
  }

  navOnTop = (event) => {
    const scroll = event.currentTarget.scrollY
    if (scroll === 0) {
      this.setState({
        onTop: true
      })
    } else {
      this.setState({
        onTop: false
      })
    }
  }

  handleScroll = (event) => {
    this.props.onScrollChange(event)
    this.navOnTop(event)
  }

  inSignIn = () => {
    this.setState({
      route: 'signin',
    })
  }

  outSignIn = () => {
    this.setState({
      route: 'home'
    })
  }

  render() {
    const items = (
    <div> 
      <div className="columns" style={{marginBottom: '0'}}>
        <div className="column">
        <NavBar 
        onRouteChange={this.props.onRouteChange} 
        isSideBar={this.props.sidebar} 
        onTop={this.state.onTop} 
        onSideBar={this.props.onSideBarManage} 
        scrollNav={this.props.scroll} 
        isItemSelect={this.props.onRouteChange} 
        activeModal={this.props.onModalManage}
        isAuthorized={this.props.authorized}
        onAuthorized={this.props.onAuthorized}
        user={this.props.user}
        /></div>
      </div>
      <div className="columns">
        <div className="column" style={{padding: "0"}}><Home /></div>
      </div>
      <div className="columns">
        <div className="column"><AboutUs /></div>
      </div>
      <div className="columns">
        <div className="column"><ContactUs /></div>
      </div>
    </div>
    );


    return (
      <Aux>
        { this.props.modal ?
        <div> 
          <Modal modalManage={this.props.onModalManage} />
          {items}
        </div>  
        : this.props.sidebar ?
        <div>
          <SideBar 
          isSideBar={this.props.sidebar} 
          SideBarManage={this.props.onSideBarManage} 
          onRouteChange={this.props.onRouteChange} 
          route={this.props.route}
          /> 
          {items}
        </div>
        : this.props.route === 'signin' ?
          <SignIn 
          onLoadUser={this.props.onLoadUser} 
          outSignIn={this.props.onRouteChange} 
          onAuthorized={this.props.onAuthorized} 
          />
        : this.props.route === 'dashboard' ?
          <Dashboard  onRouteChange={this.props.onRouteChange} />
        : <div>{items}</div>}
      <Footer />
      </Aux>
    );
  }
}

const mapStateToProps = state => {
  return {
    route: state.redui.route,
    modal: state.redui.isModal,
    scroll: state.redui.scrollNav,
    sidebar: state.redui.isSideBar,
    authorized: state.redui.authorized,
    user: state.redui.user
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onRouteChange: (item) => dispatch(UI.itemSelect(item)),
    onScrollChange: (item) => dispatch(UI.handleScrolling(item)),
    onModalManage: (stat) => dispatch(UI.modalManage(stat)),
    onSideBarManage: (stat) => dispatch(UI.sideBarManage(stat)),
    onAuthorized: (stat) => dispatch(UI.authorized(stat)),
    isStillSignin: () => dispatch(UI.isStillSigin())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);



