import React, { Component } from 'react';
import 'bulma/css/bulma.css'


class Dashboard extends Component {
    render() {
        return (
            <div className='box' style={{width: "100%", height: "100%"}}>
                <span style={{cursor: "pointer"}} onClick={() => this.props.onRouteChange('home')}><i style={{margin: "10px", opacity: "0.7"}} className="fas fa-chevron-circle-left fa-3x"></i></span>
                <div style={{display: "flex", justifyContent: "center"}}><p>UNDER MAINTENANCE</p></div>
            </div>
        )
    }
}

export default Dashboard


