import React from 'react';
import 'bulma/css/bulma.css'

const Info = () => (
    <div className="box">
        <div className="content is-small">
            <p>Do you want to learn Korean quick? this webpage is your final destination, we can give you
                the best guideline to learn from beggining and become and advanced korean speaker realy soon.
                With this course you can learn about Hangul, Vocabulary, Gramatic and more.
                Don't be ashame to ask us what do you need and we can give you private lessons.
                Your travel to Korea start here, have fun
            </p>
        </div>
    </div>
)

export default Info;


